import React, { memo } from 'react';
import { get as _get, map as _map } from 'lodash';
import { compose, withPropsOnChange, withHandlers } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import {
  Grid,
  Typography,
  FormControl,
  FormHelperText,
  TextField,
  Checkbox,
  FormControlLabel
} from '@material-ui/core';
import PropTypes from 'prop-types';

import { ALL_ROLES } from '../../../../enums/user-roles';
import withViewerData from '../../../../utils/with-viewer-data';
import Loading from '../../../../components/Loading';
import styles from './styles';

const BaseMemberForm = memo(({
  setBuildingOptionChecked, isLoading,
  formData, setFormData, classes, isSubmiting
}) => {
  if (isLoading) return <Loading />;
  const { buildingsOptions, validationErrors: errors } = formData;
  return (
    <React.Fragment>
      <Grid container direction="row" spacing={1}>
        <Grid item xs={12} sm={6}>
          <FormControl fullWidth margin="normal">
            <TextField
              error={!!errors.name}
              label="Nome"
              disabled={isSubmiting}
              value={_get(formData, 'name', '')}
              InputLabelProps={{ shrink: true }}
              onChange={(e) => setFormData({ name: e.target.value })}
              variant="outlined"
            />
            {errors.name && <FormHelperText>{errors.name}</FormHelperText>}
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormControl fullWidth margin="normal">
            <TextField
              error={!!errors.lastname}
              label="Sobrenome"
              disabled={isSubmiting}
              value={_get(formData, 'lastname', '')}
              InputLabelProps={{ shrink: true }}
              onChange={(e) => setFormData({ lastname: e.target.value })}
              variant="outlined"
            />
            {errors.lastname && <FormHelperText>{errors.lastname}</FormHelperText>}
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormControl fullWidth margin="normal">
            <TextField
              error={!!errors.email}
              label="E-mail"
              disabled
              value={_get(formData, 'email', '')}
              InputLabelProps={{ shrink: true }}
              onChange={(e) => setFormData({ email: e.target.value })}
              variant="outlined"
            />
            {errors.email && <FormHelperText>{errors.email}</FormHelperText>}
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={6}>
          <FormControl fullWidth margin="normal">
            <TextField
              type="tel"
              error={!!errors.phone}
              label="Telefone celular"
              disabled={isSubmiting}
              value={_get(formData, 'phone', '')}
              InputLabelProps={{ shrink: true }}
              onChange={(e) => {
                setFormData({
                  phone: (e.target.value || '').replace(/[\D]+/g, '').slice(0, 15)
                });
              }}
              variant="outlined"
            />
            {errors.phone && <FormHelperText>{errors.phone}</FormHelperText>}
          </FormControl>
        </Grid>
        <Grid item xs={12} sm={6}>
          <Typography variant="h6">
            Permisso
          </Typography>
          <FormControl fullWidth margin="normal">
            <TextField
              select
              SelectProps={{ native: true }}
              error={!!errors.role}
              label="Nvel de acesso"
              value={_get(formData, 'role', '')}
              disabled={isSubmiting}
              InputLabelProps={{ shrink: true }}
              onChange={(e) => setFormData({ role: e.target.value })}
              variant="outlined"
            >
              {ALL_ROLES.map(r => <option value={r.value} key={r.value}>{r.label}</option>)}
            </TextField>
            {errors.role ? <FormHelperText>{errors.role}</FormHelperText> : null}
          </FormControl>
        </Grid>

        <Grid item xs={12}>
          <Typography variant="h6">
            Locais de acesso
          </Typography>
          <FormControl fullWidth>
            {
              buildingsOptions.map((option) => (
                <FormControlLabel
                  key={option.value}
                  classes={{ label: classes.checkboxLabel }}
                  control={
                    <Checkbox
                      checked={option.checked}
                      disabled={isSubmiting}
                      onChange={(e) => {
                        setBuildingOptionChecked({
                          buildingId: option.value, checked: e.target.checked
                        });
                      }}
                      value={option.value}
                    />
                  }
                  label={option.label}
                />
              ))
            }
            {errors.building_ids && <FormHelperText>{errors.building_ids}</FormHelperText>}
          </FormControl>
        </Grid>
      </Grid>
    </React.Fragment>
  );
});
BaseMemberForm.propTypes = {
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  setBuildingOptionChecked: PropTypes.func,
  isLoading: PropTypes.bool,
  isSubmiting: PropTypes.bool,
  classes: PropTypes.object
};

const MemberForm = compose(
  withStyles(styles, { withTheme: true }),
  withViewerData,
  withHandlers({
    setBuildingOptionChecked: ({ formData, setFormData }) => ({ buildingId, checked }) => {
      const { buildingsOptions } = formData;
      setFormData({
        buildingsOptions: _map(buildingsOptions, (option) => {
          return option.value === buildingId ? { ...option, checked } : option;
        })
      });
    }
  }),
  withPropsOnChange(['viewer', 'member'], (props) => {
    const { member, viewer, setFormData } = props;
    if (!viewer) return null;

    const buildingsEnterpriseUsers = _get(member, 'enterprise_user.buildings_enterprise_users', []);
    const buildingsOptions = viewer.buildings_enterprise_users.map(viewerBeu => ({
      label: viewerBeu.building.name,
      value: viewerBeu.building.id,
      checked: buildingsEnterpriseUsers.some(beu => viewerBeu.building_id === beu.building_id)
    }));
    const formDataUpdated = {
      buildingsOptions,
      ...member
    };
    setFormData({
      ...formDataUpdated,
      previousValues: formDataUpdated
    });
    return true;
  })
)(BaseMemberForm);

export default MemberForm;
