import React, { memo } from 'react';
import { get as _get, size as _size } from 'lodash';
import { compose } from 'recompose';
import PropTypes from 'prop-types';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';

import Session from '../../../utils/session';
import withRequest from '../../../utils/with-request';
import MembersService from '../../../services/members';
import { USER_MANAGER_ROLES } from '../../../enums/user-roles';
import withNotAuthorizedHandler from '../../../utils/with-not-authorized-handler';
import Loading from '../../../components/Loading';
import MembersItemRow from '../MembersItemRow';
import styles from './styles';

const MembersTable = memo(({
  membersData, membersLoading: loading,
  classes, membersFetch, setAlert
}) => {
  const enterpriseUser = Session.getUserData();
  const loggedMemberId = _get(enterpriseUser, 'member.id');
  const members = _get(membersData, 'members');

  return (
    <div className={classes.container}>
      <Paper className={classes.paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell className={classes.tableCell}>NOME</TableCell>
              <TableCell className={classes.tableCell}>EMAIL</TableCell>
              <TableCell className={classes.tableCell}>PERMISSO DE ACESSO</TableCell>
              <TableCell className={classes.tableCell} />
            </TableRow>
          </TableHead>
          <TableBody>
            {
              loading ?
                <TableRow>
                  <TableCell colSpan={4} align="center">
                    <Loading />
                  </TableCell>
                </TableRow>
                :
                (
                  _size(members) ?
                    (members.map((member, i) => {
                      return (
                        <MembersItemRow
                          loggedMemberId={loggedMemberId}
                          refetch={membersFetch}
                          setAlert={setAlert}
                          index={i}
                          key={member.id}
                          member={member}
                          loading={loading}
                        />
                      );
                    }))
                    :
                    <TableRow>
                      <TableCell className={classes.empty} colSpan={4} align="center">
                        Nenhum usurio cadastrado
                      </TableCell>
                    </TableRow>
                )
            }
          </TableBody>
        </Table>
      </Paper>
    </div>
  );
});

MembersTable.propTypes = {
  membersLoading: PropTypes.bool,
  membersData: PropTypes.object,
  membersFetch: PropTypes.func,
  classes: PropTypes.object,
  setAlert: PropTypes.func
};

const withMembers = withRequest(MembersService.getMembers, {
  name: 'members',
  options: ({ setAlert }) => ({
    onRequestError: () => {
      setAlert({
        alertMsg: 'Erro ao carregar dados do servidor',
        alertType: 'error',
        showAlert: true
      });
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withNotAuthorizedHandler({
    condition: (viewer) => !USER_MANAGER_ROLES.includes(viewer.member.role)
  }),
  withMembers
)(MembersTable);
